.sb-DualPill {
  display: inline-block;
  font-weight: var(--sbt-font-weight-60);
  height: var(--sb-DualPill-size);
  position: relative;
  text-align: center;
}

.sb-DualPill_split_left {
  background-color: var(--sb-DualPill-color, var(--sbt-theme-color-70));
  border-bottom-left-radius: var(--sb-DualPill-size);
  border-top-left-radius: var(--sb-DualPill-size);
  color: var(--sbt-color-text-invert);
  display: inline;
  line-height: var(--sb-DualPill-size);
  padding: calc(var(--sb-DualPill-size) / 4) calc(var(--sb-DualPill-size) / 2);
  width: auto;
}

.sb-DualPill_split_right {
  background-color: var(--sb-DualPill-color, var(--sbt-theme-color-70));
  border-bottom-right-radius: var(--sb-DualPill-size);
  border-top-right-radius: var(--sb-DualPill-size);
  color: var(--sbt-color-text-invert);
  display: inline;
  line-height: var(--sb-DualPill-size);
  padding: calc(var(--sb-DualPill-size) / 4) calc(var(--sb-DualPill-size) / 2);
  width: auto;
}

.sb-DualPill_light {
  --sb-button-color: var(
    --sb-DualPill-color-darker,
    var(--sbt-theme-tertiary-color-20)
  );
  background-color: var(--sb-DualPill-color-lighter, var(--sbt-theme-color-30));
  color: var(--sb-DualPill-color, var(--sbt-theme-color-90));
}

.sb-DualPill_primary {
  --sb-DualPill-color: var(--sbt-theme-color-70);
  --sb-DualPill-color-light: var(--sbt-theme-color-40);
  --sb-DualPill-color-lighter: var(--sbt-theme-color-30);
  --sb-DualPill-color-darker: var(--sbt-theme-color-90);
  --sb-DualPill-color-darkest: var(--sbt-theme-color-100);
}

.sb-DualPill_secondary {
  --sb-DualPill-color: var(--sbt-slate-60);
  --sb-DualPill-color-light: var(--sbt-slate-30);
  --sb-DualPill-color-lighter: var(--sbt-slate-20);
  --sb-DualPill-color-darker: var(--sbt-slate-90);
  --sb-DualPill-color-darkest: var(--sbt-slate-100);
}

.sb-DualPill_base {
  --sb-DualPill-color: var(--sbt-theme-tertiary-color-50);
  --sb-DualPill-color-light: var(--sbt-theme-tertiary-color-30);
  --sb-DualPill-color-lighter: var(--sbt-theme-tertiary-color-20);
  --sb-DualPill-color-darker: var(--sbt-theme-tertiary-color-80);
  --sb-DualPill-color-darkest: var(--sbt-theme-tertiary-color-90);
}

.sb-DualPill_error {
  --sb-DualPill-color: var(--sbt-color-error-50);
  --sb-DualPill-color-light: var(--sbt-color-error-30);
  --sb-DualPill-color-lighter: var(--sbt-color-error-10);
  --sb-DualPill-color-darker: var(--sbt-red-90);
  --sb-DualPill-color-darkest: var(--sbt-red-100);
}

.sb-DualPill_success {
  --sb-DualPill-color: var(--sbt-color-success-50);
  --sb-DualPill-color-light: var(--sbt-green-30);
  --sb-DualPill-color-lighter: var(--sbt-color-success-10);
  --sb-DualPill-color-darker: var(--sbt-green-90);
  --sb-DualPill-color-darkest: var(--sbt-green-100);
}

.sb-DualPill_warning {
  --sb-DualPill-color: var(--sbt-color-warning-50);
  --sb-DualPill-color-light: var(--sbt-orange-30);
  --sb-DualPill-color-lighter: var(--sbt-color-warning-10);
  --sb-DualPill-color-darker: var(--sbt-yellow-100);
  --sb-DualPill-color-darkest: var(--sbt-orange-100);
}

.sb-DualPill_closable {
  padding-right: var(--sb-DualPill-size);

  i {
    line-height: var(--sbt-line-height-0);
  }
}

.sb-DualPill_smaller {
  --sb-DualPill-size: 8px;
  font-size: var(--sbt-font-size-20);

  &.sb-DualPill_dot {
    --sb-DualPill-size: 3px;
  }
}

.sb-DualPill_small {
  --sb-DualPill-size: 24px;
  font-size: var(--sbt-font-size-20);

  &.sb-DualPill_dot {
    --sb-DualPill-size: 8px;
  }
}

.sb-DualPill_medium {
  --sb-DualPill-size: 32px;
  font-size: var(--sbt-font-size-25);

  &.sb-DualPill_dot {
    --sb-DualPill-size: 12px;
  }
}

.sb-DualPill_large {
  --sb-DualPill-size: 40px;
  font-size: var(--sbt-font-size-30);

  &.sb-DualPill_dot {
    --sb-DualPill-size: 16px;
  }
}

.sb-DualPill_width-large {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 16ch;
}
